html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

a, ul, li {
  text-decoration: none;
}

@font-face {
  font-family: Bebas;
  src: url("../fonts/bebasneue_bold-webfont.woff2") format("woff2"), url("../fonts/bebasneue_bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background-image: url(../img/blog.svg);
}

.container {
  max-width: 63.2%;
  margin: 0 auto;
}

.container--max {
  width: 73.7%;
  margin-left: 145px;
}

.header {
  margin-top: 22px;
  font-family: Bebas, Arial, sans-serif;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.header__logo {
  float: left;
  color: #e8603c;
  font-size: 36px;
}

.header__logo span {
  color: #f3af9d;
}

.header__nav {
  float: right;
}

.nav {
  white-space: nowrap;
  text-align: right;
}

.nav__item {
  display: inline-block;
  margin: 14px 0 27px 27px;
}

.nav__link {
  color: #b9b9b9;
}

.nav__link:hover {
  color: #e8603c;
  font-size: 16px;
}

.deco {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.deco:after {
  content: " ";
  display: table;
  clear: both;
}

.deco--center {
  width: 63.4%;
  background: #e8603c;
  font-family: Bebas, Arial, sans-serif;
  font-size: 24px;
  color: #fff;
  padding: 17px;
}

.deco--left {
  margin-right: 10px;
  height: 60px;
  background: #f3af9d;
  float: left;
  width: 17.4%;
}

.deco--right {
  margin-left: 10px;
  height: 60px;
  background: #f3af9d;
  float: right;
  width: 17.4%;
}

.h2 {
  font-family: Bebas, Arial, sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  color: #e8603c;
  margin-bottom: 21px;
}

.h2--small {
  font-size: 14px;
  margin-bottom: 10px;
}

.h2--minmarg {
  margin-bottom: 3px;
}

.small-txt {
  color: #b9b9b9;
  font-size: 12px;
  font-family: Helvetica, Arial, sans-serif;
}

.p {
  font-size: 12px;
  font-family: Helvetica, Arial, sans-serif;
  color: #464646;
  line-height: 1.6;
}

.p--two {
  margin-top: 25px;
}

.section {
  padding-bottom: 100px;
}

.icon {
  position: absolute;
  width: 75px;
  height: 75px;
  background: #e8603c;
  padding: 23px;
  left: 191px;
}

.article {
  max-width: 670px;
}

.article__img img {
  width: 670px;
}

.post {
  margin-top: 25px;
}

.post:after {
  content: " ";
  display: table;
  clear: both;
}

.post__info {
  width: 25.4%;
  float: left;
  text-align: left;
}

.post__info p {
  padding-bottom: 18px;
}

.post__title {
  text-align: left;
  float: right;
  max-width: 74.6%;
}

.line {
  background: url(../img/dots.svg);
  height: 13px;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 50px;
}

.citation {
  background: #f5f5f5;
  width: 100%;
  padding: 25px 70px;
}

.citation__img {
  position: absolute;
  left: 20%;
}

.index {
  font-family: Bebas, Arial, sans-serif;
  font-size: 14px;
  color: #fff;
}

.index__num {
  display: inline-block;
  background: #f3af9d;
  width: 25px;
  height: 25px;
  margin-left: 2px;
  padding: 7px;
}

.index__num-is-active {
  background: #e8603c;
}

.navigation {
  width: 220px;
  position: absolute;
  left: 1020px;
  top: 163px;
}

.egg {
  line-height: 1.2;
  padding-bottom: 40px;
}

.egg__title {
  margin: 0;
  margin-bottom: 22px;
}

.egg__txt {
  line-height: 1.2;
}

.egg__list {
  font-size: 12px;
  font-family: Helvetica, Arial, sans-serif;
}

.egg__link {
  color: #464646;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.grid__item {
  background: #f3af9d;
  border: 1px solid #e8603c;
  width: 48px;
  height: 48px;
  margin-bottom: 6px;
}

.dynamic {
  display: flex;
  width: 220px;
  flex-wrap: nowrap;
}

.dynamic__txt {
  width: auto;
}

.dynamic__line {
  background: url(../img/dots.svg);
  height: 13px;
  margin-left: 13px;
  margin-top: 1.5px;
  flex-grow: 1;
}

.gallery .img {
  width: 670px;
  height: 300px;
  text-align: center;
  font-family: Bebas, Arial, sans-serif;
}

.gallery .slick-prev, .gallery .slick-next {
  background: url(../img/previous.svg);
  font-size: 0px;
  position: absolute;
  left: 290px;
  top: 295px;
  width: 36px;
  height: 52px;
  z-index: 1005;
}

.gallery .slick-next {
  left: 890px;
  background: url(../img/next.svg);
}

.gallery .slick-dots li {
  display: none;
}
